	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"College course on building IKEA furniture","date":"11 Jan 2004","slug":"/2004-01-11-college-course-on-building-ikea-furniture/","tags":"Y2004"},"html":"<p><a title=\"For those who have trouble with pictoral Swedish?\" href=\"http://www.margaret-marks.com/Transblawg/archives/000578.html\">College course on building IKEA furniture</a></p>"},"next":{"fields":{"title":"Blogs and German Politics","date":"11 Jan 2004","slug":"/2004-01-11-blogs-and-german-politics/"}},"previous":{"fields":{"title":"Living in the Past","date":"11 Jan 2004","slug":"/2004-01-11-living-in-the-past/"}}},{"node":{"fields":{"title":"Blogs and German Politics","date":"11 Jan 2004","slug":"/2004-01-11-blogs-and-german-politics/","tags":"Y2004"},"html":"<p>Some interesting thoughts at <a title=\"Weblogs und deutsche Politiker [jimmiz journal]\" href=\"http://jimmiz.blogg.de/eintrag.php?id=157\">jimmiz journal</a> on the (lack of) blogging and German politics (follow the links there to Nico, Heiko and Moe, I'm too lazy to type them in  here), all the more interesting since Adam Curry seems to getting <a title=\"Adam Curry's Weblog\" href=\"http://radio.weblogs.com/0001014/2004/01/06.html#a5027\">parties and politicians in The Netherlands</a> to start blogging. But Germany does have the <a title=\"SPD-Horn, Sozialdemokratische Partei Deutschlands, Distrikt Hamburg-Horn\" href=\"http://www.spd-horn.de/\">SPD in the Hamburg district of Horn</a>, which has been blogging (with Movable Type) since the beginning of December. Keep it up!</p>"},"next":{"fields":{"title":"Two Polls","date":"10 Jan 2004","slug":"/2004-01-10-two-polls/"}},"previous":{"fields":{"title":"College course on building IKEA furniture","date":"11 Jan 2004","slug":"/2004-01-11-college-course-on-building-ikea-furniture/"}}},{"node":{"fields":{"title":"Two Polls","date":"10 Jan 2004","slug":"/2004-01-10-two-polls/","tags":"Y2004"},"html":"<p>Polls don't mean anything come election day, but they are interesting nonetheless. The ARD <a title=\"tagesschau.de : Sonntagsfrage: Union verliert absolute Mehrheit\" href=\"http://www.tagesschau.de/aktuell/meldungen/0,1185,OID2827436_TYP6_THE2827398_NAV2827398_REF_BAB,00.html\">DeutschlandTREND</a>  shows the Union falling below an absolute majority in the \"Sunday question\" for the first time in 5 months, but still with 48%. Winners were not red/green (26%/10%), but the FDP (7%) and the PDS (5%!). Has the far-left flight from the SPD to the ex-communists begun? </p>\n<p>Meanwhile locally, 7 weeks before the Hamburg election <a title=\"Hamburg: Aktuelle Wahlumfrageergebnisse [jimmiz journal]\" href=\"http://jimmiz.blogg.de/eintrag.php?id=156\">Mayor von Beust and the CDU have a comfortable lead</a> (47%) over Mirow and the SPD (30%). The Greens have 11%, FDP 2%, old Schill 2%, and new Schill (Pro-DM) 4%.  With coalition partners, von Beust is only 6 points ahead of red/green, and if Schill somehow gets over 5% all bets are off.</p>"},"next":{"fields":{"title":"Flip the little Dolphin","date":"10 Jan 2004","slug":"/2004-01-10-flip-the-little-dolphin/"}},"previous":{"fields":{"title":"Blogs and German Politics","date":"11 Jan 2004","slug":"/2004-01-11-blogs-and-german-politics/"}}}],"pathPrefix":"","first":false,"last":false,"index":556,"pageCount":1101}}

